import newsItem from '../../components/news-item/news-item.vue';
export default {
	data() {
		return {
			newsList: []
		}
	},
	onLoad() {
		this.getNews()
	},
	components: {
		"news-item": newsItem,
	},
	methods: {
		async getNews() {
			const res = await this.$myRequest({
				url: "/api/getnewslist"
			})
			this.newsList = res.data.message;
			console.log(res)
		},
		goDetail(id) {
			uni.navigateTo({
				url: '/pages/news-detail/news-detail?id=' + id
			})
		}
	}
}
